import React, { useEffect, useState } from "react";
import { Breadcrumb, Spin, Table, Button, Popconfirm, Pagination } from "antd";
import { Link } from "react-router-dom";
import Title from "antd/lib/typography/Title";
import { useSelector, useDispatch } from "react-redux";
import { DeleteOutlined, EditOutlined } from "@ant-design/icons";
import { FetchURL, openNotificationWithIcon } from "../../../util/help";
import { actHideLoading, actShowLoading } from "../../../actions/actionLoading";
import { deletePhieuChi, fetchPhieuChi } from "../../../actions/actionPhieuChiTamUng";
import axios from "../../../util/api";

function PhieuChiTamUng() {
  const [state, setState] = useState({ total_page: 1 });

  const isLoading = useSelector((state) => state.isLoading);
  const phieuchitamung = useSelector((state) => state.phieuchitamung);

  const dispatch = useDispatch();
  const showLoading = () => dispatch(actShowLoading());
  const hideLoading = () => dispatch(actHideLoading());
  const Fetch = (data) => dispatch(fetchPhieuChi(data));
  const DeletePhieuChiTamUng = (LENDER_NO) => dispatch(deletePhieuChi(LENDER_NO));

  useEffect(() => {
    onChangePage();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const Delete = (LENDER_NO) => {
    var params = {
        LENDER_NO: LENDER_NO,
    };
    showLoading();
    axios
      .post("payment/lender/remove", params)
      .then((res) => {
        if (res.status === 200) {
          if (res.data.success === true) {
            DeletePhieuChiTamUng(LENDER_NO);
            hideLoading();
            openNotificationWithIcon(
              "success",
              `Bạn đã xóa thành công ${LENDER_NO}`
            );
          }
        }
      })
      .catch((err) => console.log(err));
  };

  const onChangePage = (page) => {
    var newPage = 1;
    if (page) {
      newPage = page;
    }
    showLoading();
    fetch(FetchURL + `payment/lender/page=${newPage}`)
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
        if (data.success === true) {
          Fetch(data.data);
          setState({ total_page: data.total_page });
          hideLoading();
        }
      })
      .catch((err) => console.log(err));
  };

  const columns = [
    {
      title: "Advance No",
      dataIndex: "LENDER_NO",
      key: "LENDER_NO",
    },
    {
      title: "Type",
      dataIndex: "LENDER_NAME",
      key: "LENDER_NAME",
    },
    {
      title: "Job No",
      dataIndex: "JOB_NO",
      key: "JOB_NO",
    },
    {
      render: (text, record) => (
        <>
          <Link to={`/Dasboard/phieuchitamung/edit/${record.LENDER_NO}`}>
            <Button
              type="primary"
              style={{
                backgroundColor: "#faad14",
                borderColor: "#faad14",
                marginRight: 20,
              }}
              icon={<EditOutlined />}
            >
              <span style={{ marginLeft: 8, color: "white" }}>Sửa</span>
            </Button>
          </Link>

          <Popconfirm
            title="Bạn có chắn chắn xóa?"
            onConfirm={() => Delete(record.LENDER_NO)}
          >
            <Button type="primary" danger icon={<DeleteOutlined />}>
              Xóa
            </Button>
          </Popconfirm>
        </>
      ),
    },
  ];

  return (
    <Spin size="large" spinning={isLoading}>
      <Breadcrumb style={{ margin: "16px 0" }}>
        <Breadcrumb.Item>Quản Lý Thu Chi</Breadcrumb.Item>
        <Breadcrumb.Item>Phiếu Chi Tạm Ứng</Breadcrumb.Item>
      </Breadcrumb>
      <div
        className="site-layout-background"
        style={{ padding: 24, minHeight: 360 }}
      >
        <div style={{ textAlign: "center" }}>
          <Title style={{ color: "#1890ff" }}>Phiếu Chi Tạm Ứng</Title>
        </div>
        <Button type="primary" style={{ marginBottom: 20 }}>
          <Link to="/Dasboard/phieuchitamung/create">Tạo mới</Link>
        </Button>
        <Table
          dataSource={phieuchitamung}
          columns={columns}
          rowKey={columns[0].key.toString()}
          pagination={false}
        />
        <Pagination
          total={state.total_page}
          defaultCurrent={1}
          onChange={onChangePage}
          style={{ marginTop: 20 }}
        />
      </div>
    </Spin>
  );
}

export default PhieuChiTamUng;
