import React, { useEffect, useState } from "react";
import {
  Table,
  Input,
  InputNumber,
  Popconfirm,
  Form,
  Select,
  Button,
  Spin,
} from "antd";
import "./tableEdit.css";
import {
  payDescription,
  orderType,
  openNotificationWithIcon,
} from "../../../util/help";
import axios from "../../../util/api";
import { useSelector, useDispatch } from "react-redux";
import { actHideLoading, actShowLoading } from "../../../actions/actionLoading";

const { Option } = Select;

const CreatItemJobOrder = ({ job_d, id, getDataDetail }) => {
  const [form] = Form.useForm();
  const [state, setState] = useState({ data: [] });
  const [editingKey, setEditingKey] = useState("");
  const [changeSelect, setChangSelect] = useState("select");

  const isLoading = useSelector((state) => state.isLoading);

  const dispatch = useDispatch();

  const showLoading = () => dispatch(actShowLoading());
  const hideLoading = () => dispatch(actHideLoading());

  const EditableCell = ({
    editing,
    dataIndex,
    title,
    inputType,
    record,
    index,
    children,
    ...restProps
  }) => {
    const inputNode =
      inputType === "selectType" ? (
        <Select>
          <Option key="Our Company Pay">Our Company Pay</Option>
          <Option key="Pay In Advance">Pay In Advance</Option>
          <Option key="Trucking Fee">Trucking Fee</Option>
          <Option key="Deposit fee">Cược Cont</Option>
          <Option key="Deposit fix fee">Cược Sửa Chữa Cont</Option>
          <Option key="Refund Customer">Refund Khách Hàng</Option>
          <Option key="Refund Carriers">Refund Hãng Tàu</Option>
          <Option key="Refund Agent">Refund Đại Lý</Option>
        </Select>
      ) : inputType === "selectDes" ? (
        changeSelect === "Thêm" ? (
          <Input />
        ) : (
          <Select
            showSearch
            optionFilterProp="children"
            filterOption={(input, option) =>
              option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }
            onSelect={(value) => setChangSelect(value)}
          >
            {payDescription.map((item) => {
              return <Option key={item}>{item}</Option>;
            })}
          </Select>
        )
      ) : inputType === "number" ? (
        <InputNumber />
      ) : (
        <Input />
      );
    return (
      <td {...restProps}>
        {editing ? (
          <Form.Item
            name={dataIndex}
            style={{
              margin: 0,
            }}
          >
            {inputNode}
          </Form.Item>
        ) : (
          children
        )}
      </td>
    );
  };

  useEffect(() => {
    var newData = job_d;
    for (var i = 0; i < newData.length; i++) {
      newData[i].key = i;
    }
    setState({ ...state, data: newData });
  }, [job_d]);

  const isEditing = (record) => record.key === editingKey;

  const edit = (record) => {
    form.setFieldsValue({
      ORDER_TYPE: "",
      ORDER_TYPE_NAME: "",
      DESCRIPTION: "",
      PORT_AMT: "",
      INDUSTRY_ZONE_AMT: "",
      NOTE: "",
      ...record,
    });
    setEditingKey(record.key);
  };

  const cancel = () => {
    setChangSelect("select");
    setEditingKey("");
  };

  const save = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...state.data];
      const index = newData.findIndex((item) => key === item.key);

      if (index > -1) {
        const item = newData[index];
        newData.splice(index, 1, { ...item, ...row });
        const typeTest = orderType.find(
          (i) => i.name === newData[index].ORDER_TYPE_NAME
        );
        newData[index].ORDER_TYPE = typeTest.type;
        showLoading();
        if (newData[index].SER_NO) {
          newData[index].TYPE = "JOB_ORDER";
          newData[index].JOB_NO = id;
          newData[index].THANH_TOAN_MK = "N";
          newData[index].MODIFY_USER = localStorage.getItem("INPUT_USER");
          newData[index].REV_TYPE = "Y";
          
          axios
            .post("file/job-order/edit-d", newData[index])
            .then((res) => {
              if (res.status === 200) {
                if (res.data.success === true) {
                  getDataDetail(id);
                  hideLoading();
                  openNotificationWithIcon("success", "Chỉnh Sửa Thành Công");
                }
              }
            })
            .catch((err) => console.log(err));
        } else {
          newData[index].TYPE = "JOB_ORDER";
          newData[index].JOB_NO = id;
          newData[index].THANH_TOAN_MK = "N";
          newData[index].BRANCH_ID = localStorage.getItem("BRANCH_ID");
          newData[index].INPUT_USER = localStorage.getItem("INPUT_USER");
          axios
            .post("file/job-order/add-d", newData[index])
            .then((res) => {
              if (res.status === 200) {
                if (res.data.success === true) {
                  getDataDetail(id);
                  hideLoading();
                  openNotificationWithIcon("success", "Tạo Thành Công");
                }
              }
            })
            .catch((err) => console.log(err));
        }

        setState({ ...state, data: newData });
        setEditingKey("");
      } else {
        newData.push(row);
        setState({ ...state, data: newData });
        setEditingKey("");
      }
    } catch (errInfo) {
      console.log("Validate Failed:", errInfo);
    }
  };

  const Delete = async (key) => {
    try {
      const row = await form.validateFields();
      const newData = [...state.data];
      const index = newData.findIndex((item) => key.key === item.key);

      if (index > -1) {
        const params = {
          JOB_NO: id,
          ORDER_TYPE: newData[index].ORDER_TYPE,
          SER_NO: newData[index].SER_NO,
        };
        showLoading();
        axios
          .post("file/job-order/remove-d", params)
          .then((res) => {
            if (res.status === 200) {
              if (res.data.success === true) {
                newData.splice(index, 1);
                setState({ ...state, data: newData });
                setEditingKey("");
                hideLoading();
                openNotificationWithIcon("success", res.data.msg);
              }
            }
          })
          .catch((err) => console.log(err));
      }
    } catch (errInfo) {
      console.log("Validate Failed:", errInfo);
    }
  };

  const handleAdd = async () => {
    const row = await form.validateFields();
    const newData = [...state.data];
    row.key = newData.length;
    newData.push(row);
    setEditingKey("");
    setState({ ...state, data: newData });
  };

  const columns = [
    {
      title: "Loại",
      dataIndex: "ORDER_TYPE_NAME",
      width: "15%",
      editable: true,
    },
    {
      title: "Mô tả",
      dataIndex: "DESCRIPTION",
      width: "25%",
      editable: true,
    },
    {
      title: "Port Amt",
      dataIndex: "PORT_AMT",
      width: "15%",
      editable: true,
    },
    {
      title: "Industry",
      dataIndex: "INDUSTRY_ZONE_AMT",
      width: "15%",
      editable: true,
    },
    {
      title: "Note",
      dataIndex: "NOTE",
      width: "15%",
      editable: true,
    },
    {
      title: "Action",
      dataIndex: "operation",
      render: (_, record) => {
        const editable = isEditing(record);
        return editable ? (
          <span>
            <Button
              accessKey='s'
              type="link"
              onClick={() => save(record.key)}
              style={{
                marginRight: 8,
              }}
            >
              Save
            </Button>
            <Popconfirm title="Sure to cancel?" onConfirm={cancel}>
              <a>Cancel</a>
            </Popconfirm>
          </span>
        ) : (
          <>
            <a onClick={() => edit(record)}>Edit</a>

            <Popconfirm
              title="Bạn có chắn chắn xóa?"
              onConfirm={() => Delete(record)}
            >
              <Button type="link">Delete</Button>
            </Popconfirm>
          </>
        );
      },
    },
  ];
  const mergedColumns = columns.map((col) => {
    if (!col.editable) {
      return col;
    }

    return {
      ...col,
      onCell: (record) => ({
        record,
        inputType:
          col.dataIndex === "ORDER_TYPE_NAME"
            ? "selectType"
            : col.dataIndex === "DESCRIPTION"
            ? "selectDes"
            : col.dataIndex === "NOTE"
            ? "text"
            : "number",
        dataIndex: col.dataIndex,
        title: col.title,
        editing: isEditing(record),
      }),
    };
  });
  return (
    <Form form={form} component={false}>
      <Button onClick={handleAdd} type="primary" style={{ marginBottom: 16 }}>
        Add a row
      </Button>
      <Spin size="large" spinning={isLoading}>
        <Table
          components={{
            body: {
              cell: EditableCell,
            },
          }}
          bordered
          dataSource={state.data}
          columns={mergedColumns}
          // rowClassName="editable-row"
          pagination={false}
        />
      </Spin>
    </Form>
  );
};

export default CreatItemJobOrder;
