import React, { useState } from "react";
import {
  FileOutlined,
  DatabaseOutlined,
  UnorderedListOutlined,
} from "@ant-design/icons";
import { Layout, Menu } from "antd";
import { Link, useHistory } from "react-router-dom";
const { Sider } = Layout;
const { SubMenu } = Menu;

const menuBasicData = [
  {
    name: "Khách Hàng",
    to: "/Dasboard/khachhang",
    exact: true,
  },
  {
    name: "Nhân Viên",
    to: "/Dasboard/nhanvien",
    exact: true,
  },
  {
    name: "Các Loại Chi Phí",
    to: "/Dasboard/chiphi",
    exact: true,
  },
  {
    name: "Hãng Tàu",
    to: "/Dasboard/hangtau",
    exact: true,
  },
  {
    name: "Nhà Xe",
    to: "/Dasboard/nhaxe",
    exact: true,
  },
  {
    name: "Đại Lý",
    to: "/Dasboard/daily",
    exact: true,
  },
];

const menuQuanLyHoSo = [
  {
    name: "Tạo Phiếu Theo Dõi",
    to: "/Dasboard/taophieutheodoi",
    exact: true,
  },
  {
    name: "Tạo Job Order",
    to: "/Dasboard/taojoborder",
    exact: true,
  },
  {
    name: "Tạo Job Book Tàu",
    to: "/Dasboard/taojobbooktauf",
    exact: true,
  },
  {
    name: "Duyệt Job Order",
    to: "/Dasboard/duyetjoborder",
    exact: true,
  },
  {
    name: "Báo Biểu Hồ Sơ",
    to: "/Dasboard/baobieuhoso",
    exact: true,
  },
];

const menuQuanLyThuChi = [
  {
    name: "Phiếu Chi Tạm Ứng",
    to: "/Dasboard/phieuchitamung",
    exact: true,
  },
  {
    name: "Yêu Cầu Thanh Toán",
    to: "/Dasboard/yeucauthanhtoan",
    exact: true,
  },
  {
    name: "Duyệt Thanh Toán",
    to: "/Dasboard/duyetthanhtoan",
    exact: true,
  },
  {
    name: "Tra Cứu Dữ Liệu",
    to: "/Dasboard/tracuudulieu",
    exact: true,
  },
  {
    name: "Chi Phí Cược Tàu/Cont",
    to: "/Dasboard/chiphicuoctaucont",
    exact: true,
  },
]

export default function SiderComponent({ routes }) {
  let history = useHistory();

  const [state, setState] = useState(false);

  const onCollapse = (collapsed) => {
    setState(collapsed);
  };

  const MenuItem = (menus) => {
    var result = null;
    if (routes.length > 0) {
      result = menus.map((menu) => {
        return (
          <Menu.Item key={menu.to}>
            <span>{menu.name}</span>
            <Link to={menu.to} />
          </Menu.Item>
        );
      });
    }
    return result;
  };

  return (
    <Sider collapsible collapsed={state} onCollapse={onCollapse}>
      <div className="logo" />
      <Menu
        theme="dark"
        defaultOpenKeys={["sub1"]}
        defaultSelectedKeys={[history.location.pathname]}
        mode="inline"
      >
        <SubMenu key="sub1" icon={<DatabaseOutlined />} title="Dữ Liệu Cơ Bản">
          {MenuItem(menuBasicData)}
        </SubMenu>

        <SubMenu
          key="sub2"
          icon={<UnorderedListOutlined />}
          title="Quản Lý Hồ Sơ"
        >
          {MenuItem(menuQuanLyHoSo)}
        </SubMenu>
        <SubMenu key="sub3" icon={<FileOutlined />} title="Quản Lý Thu Chi">
         {MenuItem(menuQuanLyThuChi)}
        </SubMenu>
      </Menu>
    </Sider>
  );
}
